import Vue from 'vue'
import Router from 'vue-router'
// 注册登录
const submit = () => import('../components/login/submit');
const forget = () => import('../components/login/forget');
const login = () => import('../components/login/login');
// 主页面

const home = () => import('../components/home');
const baldetail = () => import('../components/personCenter/baldetail');
const index = () => import('../components/homePage/index');
const count = () => import('../components/homePage/count');
const countInfo = () => import('../components/homePage/countInfo');
const countBuy = () => import('../components/homePage/countBuy');
const goodInfo = () => import('../components/homePage/goodInfo');
const buy = () => import('../components/homePage/buy');
const goodsList = () => import('../components/homePage/goodsList');
const setShop = () => import('../components/setShop/setShop');
const store = () => import('../components/setShop/store');
const defenShop = () => import('../components/defenShop/defenShop');
const defenGoodInfo = () => import('../components/defenShop/defenGoodInfo');
const mixtureInfo = () => import('../components/defenShop/mixtureInfo');
const defenBuy = () => import('../components/defenShop/defenBuy');
const mixBuy = () => import('../components/defenShop/mixBuy');
const convert = () => import('../components/defenShop/convertRecord');
const shopCar = () => import('../components/shopCar/shopping');
const setOrder = () => import('../components/shopCar/setOrder');
const info = () => import('../components/personCenter/person');
const sign = () => import('../components/personCenter/sign');
const order = () => import('../components/personCenter/order');
const obligation = () => import('../components/personCenter/obligation');
const unshipped = () => import('../components/personCenter/unshipped');
const unreceived = () => import('../components/personCenter/unreceived');
const completed = () => import('../components/personCenter/completed');
const wallet = () => import('../components/personCenter/wallet');
const balance = () => import('../components/personCenter/balance');
const vip = () => import('../components/personCenter/vip');
const jifen = () => import('../components/personCenter/jifen');
const duihuan = () => import('../components/personCenter/duihuan');
const duihuanjilu = () => import('../components/personCenter/duihuanjilu');
const lottery = () => import('../components/personCenter/lottery');
const seckill = () => import('../components/personCenter/seckill');
const team = () => import('../components/personCenter/team');
const kernel = () => import('../components/personCenter/kernel');
const ear = () => import('../components/personCenter/ear');
const rank = () => import('../components/personCenter/rank');
const invite = () => import('../components/personCenter/invite');
const inviteCode = () => import('../components/personCenter/inviteCode');
const withdraw = () => import('../components/personCenter/withdraw');
const withdrawRecord = () => import('../components/personCenter/withdrawRecord');
const setPayPass = () => import('../components/personCenter/setPayPass');
const payword = () => import('../components/personCenter/payword');
const setting = () => import('../components/personCenter/setting/setting');
const bindCard = () => import('../components/personCenter/setting/bankCard');
const bankList = () => import('../components/personCenter/setting/bankList');
const withBankList = () => import('../components/personCenter/setting/withBankList');
const newsite = () => import('../components/address/newsite');
const site = () => import('../components/address/site');
const gift = () => import('../components/gift/getGift');
const delivery = () => import('../components/gift/delivery');
const packRecord = () => import('../components/gift/packRecord');
const myShop = () => import('../components/myShop/myShop');
const wxcode = () => import('../components/wxcode');
const wuliu = () => import('../components/personCenter/wuliu');
const orders = () => import('../components/personCenter/orders');
const orderdetail = () => import('../components/personCenter/orderdetail');
const duihuandetail = () => import('../components/personCenter/duihuandetail');
const confirm = () => import('../components/personCenter/confirm');
const userqr = () => import('../components/personCenter/userqr');
Vue.use(Router)

export default new Router({
  routes: [
//  {
//    path: '/submit',
//    name: 'submit',
//    component: submit
//  },
//  {
//    path: '/forget',
//    name: 'forget',
//    component: forget
//  },

//  {
//    path: '/login',
//    name: 'login',
//    component: login
//  },
    {
      path: '/',
      component: home,
      children: [
        {
          path: '',
          redirect: '/index'
        },
        {
            path: '/index',
            name: 'Index',
            component: index
        },
        {
          path: '/setShop',
          name: 'setShop',
          component: setShop
        },
        {
            path: '/myShop',
            name: 'myShop',
            component: myShop
        },
        {
            path: '/defenShop',
            name: 'defenShop',
            component: defenShop
        },
        {
            path: '/shopCar',
            name: 'shopCar',
            component: shopCar
        },
        {
            path: '/info',
            name: 'info',
            component: info
        }
        ],
    },
    {
      path: '/count',
      name: 'count',
      component: count
    },
//  {
//    path: '/goodInfo/:id',
//    name: 'goodInfo',
//    component: goodInfo
//  },
		{
//    path: '/goodInfo',
			path: '/goodInfo/:id',
      name: 'goodInfo',
      component: goodInfo
    },
    {
      path: '/sign',
      name: 'sign',
      component: sign
    },
    {
      path: '/order/:id',
      name: 'order',
      component: order
    },
    {
      path: '/obligation/:id',
      name: 'obligation',
      component: obligation
    },
    {
      path: '/unshipped/:id',
      name: 'unshipped',
      component: unshipped
    },
    {
      path: '/unreceived/:id',
      name: 'unreceived',
      component: unreceived
    },
    {
      path: '/completed/:id',
      name: 'completed',
      component: completed
    },
    {
      path: '/gift',
      name: 'gift',
      component: gift
    },
    {
      path: '/store/:id',
      name: 'store',
      component: store
    },
    {
      path: '/wallet',
      name: 'wallet',
      component: wallet
    },
    {
      path: '/balance',
      name: 'balance',
      component: balance
    },
    {
      path: '/baldetail',
      name: 'baldetail',
      component: baldetail
    },
    {
      path: '/vip',
      name: 'vip',
      component: vip
    },
    {
      path: '/jifen',
      name: 'jifen',
      component: jifen
    },
    {
      path: '/duihuan',
      name: 'duihuan',
      component: duihuan
    },
    {
      path: '/lottery',
      name: 'lottery',
      component: lottery
    },
    {
      path: '/seckill',
      name: 'seckill',
      component: seckill
    },
    {
      path: '/team',
      name: 'team',
      component: team
    },
    {
      path: '/kernel',
      name: 'kernel',
      component: kernel
    },
    {
      path: '/ear',
      name: 'ear',
      component: ear
    },
    {
      path: '/rank',
      name: 'rank',
      component: rank
    },
    {
      path: '/invite',
      name: 'invite',
      component: invite
    },
    {
      path: '/newsite/:id',
      name: 'newsite',
      component: newsite
    },
    {
      path: '/site',
      name: 'site',
      component: site
    },
    {
      path: '/setOrder/:id',
      name: 'setOrder',
      component: setOrder
    },
    {
      path: '/delivery/:id/:type',
      name: 'delivery',
      component: delivery
    },
    {
      path: '/withdraw',
      name: 'withdraw',
      component: withdraw
    },
    {
      path: '/withdrawRecord',
      name: 'withdrawRecord',
      component: withdrawRecord
    },
    {
      path: '/buy/:id/:num',
      name: 'buy',
      component: buy
    },
    {
      path: '/goodsList',
      name: 'goodsList',
      component: goodsList
    },
    {
      path: '/defenGoodInfo/:id/:money',
      name: 'defenGoodInfo',
      component: defenGoodInfo
    },
    {
      path: '/mixtureInfo/:id/:money',
      name: 'mixtureInfo',
      component: mixtureInfo
    },
    {
      path: '/convert',
      name: 'convert',
      component: convert
    },
    {
      path: '/inviteCode',
      name: 'inviteCode',
      component: inviteCode
    },
    {
      path: '/defenBuy/:id/:num',
      name: 'defenBuy',
      component: defenBuy
    },
    {
      path: '/mixBuy/:id/:num',
      name: 'mixBuy',
      component: mixBuy
    },
    {
      path: '/countInfo/:id',
      name: 'countInfo',
      component: countInfo
    },
    {
      path: '/countBuy/:id/:num',
      name: 'countBuy',
      component: countBuy
    },
    {
      path: '/packRecord',
      name: 'packRecord',
      component: packRecord
    },
    {
      path: '/payword/:type',
      name: 'payword',
      component: payword
    },
    {
      path: '/setPayPass',
      name: 'setPayPass',
      component: setPayPass
    },
    {
      path: '/setting',
      name: 'setting',
      component: setting
    },
    {
      path: '/bindCard/:id',
      name: 'bindCard',
      component: bindCard
    },
    {
      path: '/bankList',
      name: 'bankList',
      component: bankList
    },{
      path: '/withBankList',
      name: 'withBankList',
      component: withBankList
    },
    {
      path: '/wxcode',
      name: 'Wxcode',
      component: wxcode
    },
    {
      path: '/wuliu',
      name: 'wuliu',
      component: wuliu
    },
    {
      path: '/orderdetail',
      name: 'orderdetail',
      component: orderdetail
    },
    {
      path: '/orders',
      name: 'orders',
      component: orders
    },
    {
      path: '/confirm',
      name: 'confirm',
      component: confirm
    },
    {
      path: '/duihuanjilu',
      name: 'duihuanjilu',
      component: duihuanjilu
    },
    {
      path: '/duihuandetail/:id',
      name: 'duihuandetail',
      component: duihuandetail
    },
    {
      path: '/userqr',
      name: 'userqr',
      component: userqr
    }
  ]
})



