import Vue from 'vue'
import router from '../router/index'
import axios from 'axios'
import qs from 'qs'

axios.interceptors.request.use(config => {
  // loading
  const token = localStorage.getItem('token');
// 判断请求的类型
// 如果是post请求就把默认参数拼到data里面
// 如果是get请求就拼到params里面
if(config.method === 'post') {
  let data = qs.parse(config.data);
  config.data = qs.stringify({
    token: token,
    ...data
})
} else if(config.method === 'get') {
  let data = config.params;
  config.params = {
    token: token,
    ...data
}
}
return config
}, error => {
  return Promise.reject(error)
});
axios.interceptors.response.use(response => {
  return response
}, error => {
  return Promise.resolve(error.response)
});
function checkStatus (response) {
  // loading
  // 如果http状态码正常，则直接返回数据
  if (response && (response.status === 200 || response.status === 304 || response.status === 400)) {
      if (response.data.code === 11111) {
      	
      	 let redirectUrl = encodeURIComponent('http://nhsz.xiguawenhua.com/shop/#/wxcode');
  window.location.href='https://open.weixin.qq.com/connect/oauth2/authorize?appid='+response.data.id+'&redirect_uri='+redirectUrl+'&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect'
  return
//      router.push('/submit')
      } return response;
    // 如果不需要除了data之外的数据，可以直接 return response.data
  } else
  // 异常状态下，把错误信息返回去
  return {
    status: -404,
    msg: '网络异常'
  }
}
function checkCode (res) {
  // 如果code异常(这里已经包括网络错误，服务器错误，后端抛出的错误)，可以弹出一个错误提示，告诉用户
  if (res.status === -404) {
    // alert(res.msg)
  }
  if (res.data && (!res.data.success)) {
    // alert(res.data.error_msg)
  }
  return res
}
const baseUrl = "http://nhsz.xiguawenhua.com";
// let baseUrl = '';
export default {
  install(Vue,options)
  {
    Vue.prototype.post=(url, data,session)=>{
    return axios({
      method: 'post',
      baseURL: baseUrl,
      url,
      data: qs.stringify(data),
      timeout: 50000,
      withCredentials:false,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
      }
    }).then(
      (response) => {
      return checkStatus(response)
    }
  ).then(
      (res) => {
      return checkCode(res)
    }
  )
  };
    Vue.prototype.get=(url, params) =>{
    return axios({
      method: 'get',
      baseURL:baseUrl,
      url,
      params, // get 请求时带的参数
      timeout: 50000
    }).then(
      (response) => {
      return checkStatus(response)
    }
  ).then(
      (res) => {
      return checkCode(res)
    }
  )
  };
    Vue.prototype.all=([callback1,callback2,callback])=>{
    return axios.all([callback1,callback2,callback]).then(axios.spread(function(acct,perms,res){
      return [acct,perms,res]
    }))
  }
  },
  post(url, data){
    return axios({
      method: 'post',
      baseURL: baseUrl,
      url,
      data: qs.stringify(data),
      timeout: 50000,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
      }
    }).then(
      (response) => {
      return checkStatus(response)
    }
  ).then(
      (res) => {
      return checkCode(res)
    }
  )
  },
  get(url, params){
    return axios({
      method: 'get',
      baseURL: baseUrl,
      url,
      params, // get 请求时带的参数
      timeout: 50000
    }).then(
      (response) => {
      return checkStatus(response)
    }
  ).then(
      (res) => {
      return checkCode(res)
    }
  )
  }
}



