// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import check from './config/check'
import api from './config/api'
import count from './config/count'
import image from './config/url'
import store from './strore/store'
import Num from './decimals'
Vue.use(api);
Vue.prototype.check = check.checkRules;
Vue.prototype.countTime = count.countDown;
Vue.prototype.$Num = Num;

Vue.prototype.imageURL = image.setImageUrl;
Vue.config.productionTip = false
//引入ui组件
import MintUI from 'mint-ui';
import 'mint-ui/lib/style.css'
Vue.use(MintUI);
import { Toast,Indicator} from 'mint-ui';
Vue.use(Indicator);
Vue.prototype.$indicator = Indicator;
//引入md5
import md5 from 'js-md5';
Vue.prototype.md5 = md5;
/* eslint-disable no-new */
//注册全局过滤器
Vue.filter('time', function (value) {
  if (value !== undefined) {
    let date = new Date(value*1000);
    let year = date.getFullYear(),
      month = date.getMonth()+1,
      day = date.getDate(),
      hour = date.getHours(),
      minute = date.getMinutes(),
      second = date.getSeconds();
    return year+'-'+month+'-'+day+'  '+hour+ ':'+ minute +":"+second
  }
});
router.beforeEach((to, from, next) => {
  let expires = localStorage.getItem('expires');
  let date = Date.now();
  if (expires !== null) {
    if (expires > date) {
      next()
    } else {
      localStorage.clear();
      next({
        path: '/submit',
        query: { redirect: to.fullPath }
      })
    }
  } else {
    next() // 确保一定要调用 next()
  }
});
new Vue({
  el: '#app',
  router,
  store,
  components: {App},
  template: '<App/>',
});



