
class checkRules {
  constructor() {}
  static  isPoneAvailable(phone) {
    //手机号正则
    let myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
    return myreg.test(phone);
  }
  static isPassWordAvailable(pass) {
    let len = pass.length;
    return 6 <= len && len <= 18;
  }
  static isCard(num) {
    let len = num.length;
    let reg = /^\d*$/;
      return reg.test(num) && (16<len && len < 19);
  }
}
export default  {checkRules}




